:root {
  --black-color: #000;
  --black-light-color: #202024;
  --black-pagination-color: #111;
  --grey-color: #b4afaf;
  --white-color: #fff;
  --violet-color: #4f2ee8;
  --yellow-color: #eac645;
  --scroll-color: #e0e0e0;
  --transition: .7s cubic-bezier(.4, 0, .2, 1);
}

body {
  letter-spacing: .02em;
  background-color: var(--white-color);
  color: var(--black-color);
  min-width: 320px;
  font-family: DM Sans, sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
