.switch {
  background-color: #fff;
  min-width: 41px;
  height: 21px;
  padding-top: 5px;
  padding-left: 10px;
  display: inline-block;
  position: relative;
  top: 1px;
}

.switch-slider {
  cursor: pointer;
  background-image: linear-gradient(to bottom, var(--violet-color), #ecebeb);
  border-radius: 34px;
  position: absolute;
  inset: 0;
}

.switch-slider:before {
  content: "";
  cursor: pointer;
  background-color: #e8e6e6;
  border-radius: 34px;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 2px;
}

.theme:focus + .switch-slider {
  box-shadow: 0 0 2px #000;
}

.theme:checked + .switch-slider {
  background-image: linear-gradient(to bottom, var(--violet-color), #686868);
  transition: background-image linear-gradient(to bottom, var(--violet-color), #686868);
}

.theme:checked + .switch-slider:before {
  transform: translateX(19px);
}

